.hero {
  background-color: #f8e71c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero-content {
  background-color: #9b5ea5;
  padding: 32px;
}

@media (max-width: 767px) {
  .hero-content {
    padding: 32px 16px;
  }
}

.hero-title {
  font-size: 42px;
  line-height: 1.6em;
  text-align: center;
  text-transform: uppercase;
}

.hero-buttons {
  display: flex;
  justify-content: space-between;
}

.graphOne {
  margin-bottom: 50px;
}

.graphOne .graph-content {
  display: flex;
  flex-direction: column;
}

@media (max-width: 767px) {
  .graphOne .graph-content {
    font-size: 12px;
  }
}

.go-bars {
  width: calc(100% - 72px);
  padding-left: 32px;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 1059px) {
  .go-bars {
    padding-left: 16px;
  }
}

@media (max-width: 767px) {
  .go-bars {
    width: calc(100% - 32px);
    padding-left: 8px;
  }
}

.go-bars-flex {
  width: 100%;
  height: 600px;
  display: flex;
  justify-content: space-between;
}

.go-bars-left {
  color: black;
  width: 72px;
  height: 600px;
  padding: 0 16px 0 0;
  border-right: 2px solid #CCC;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}

@media (max-width: 767px) {
  .go-bars-left {
    width: 32px;
    padding: 0 8px 0 0;
  }
}

.go-bars-bottom {
  color: black;
  width: calc(100% - 70px);
  padding: 16px 0 0 34px;
  border-top: 2px solid #CCC;
  display: flex;
  justify-content: space-between;
  margin-left: auto;
}

@media (max-width: 767px) {
  .go-bars-bottom {
    width: calc(100% - 30px);
  }
}

.go-bars-year {
  width: 13.3333%;
  text-align: center;
}

.go-bar {
  width: calc((100% / 6) - 16px * 2);
  position: relative;
}

@media (max-width: 1059px) {
  .go-bar {
    width: calc((100% / 6) - 16px);
  }
}

@media (max-width: 767px) {
  .go-bar {
    width: calc((100% / 6) - 8px);
  }
}

.go-bar:nth-child(1) .go-bar-men {
  transition: transform 500ms cubic-bezier(0, 0, 0.56, 1.26) 0ms;
}

.go-bar:nth-child(1) .go-bar-women {
  transition: transform 250ms cubic-bezier(0, 0, 0.56, 1.26) 250ms;
}

.go-bar:nth-child(1) .go-bar-percentage {
  transition: opacity 250ms ease-out 250ms;
}

.go-bar:nth-child(2) .go-bar-men {
  transition: transform 500ms cubic-bezier(0, 0, 0.56, 1.26) 250ms;
}

.go-bar:nth-child(2) .go-bar-women {
  transition: transform 250ms cubic-bezier(0, 0, 0.56, 1.26) 500ms;
}

.go-bar:nth-child(2) .go-bar-percentage {
  transition: opacity 250ms ease-out 500ms;
}

.go-bar:nth-child(3) .go-bar-men {
  transition: transform 500ms cubic-bezier(0, 0, 0.56, 1.26) 500ms;
}

.go-bar:nth-child(3) .go-bar-women {
  transition: transform 250ms cubic-bezier(0, 0, 0.56, 1.26) 750ms;
}

.go-bar:nth-child(3) .go-bar-percentage {
  transition: opacity 250ms ease-out 750ms;
}

.go-bar:nth-child(4) .go-bar-men {
  transition: transform 500ms cubic-bezier(0, 0, 0.56, 1.26) 750ms;
}

.go-bar:nth-child(4) .go-bar-women {
  transition: transform 250ms cubic-bezier(0, 0, 0.56, 1.26) 1000ms;
}

.go-bar:nth-child(4) .go-bar-percentage {
  transition: opacity 250ms ease-out 1000ms;
}

.go-bar:nth-child(5) .go-bar-men {
  transition: transform 500ms cubic-bezier(0, 0, 0.56, 1.26) 1000ms;
}

.go-bar:nth-child(5) .go-bar-women {
  transition: transform 250ms cubic-bezier(0, 0, 0.56, 1.26) 1250ms;
}

.go-bar:nth-child(5) .go-bar-percentage {
  transition: opacity 250ms ease-out 1250ms;
}

.go-bar:nth-child(6) .go-bar-men {
  transition: transform 500ms cubic-bezier(0, 0, 0.56, 1.26) 1250ms;
}

.go-bar:nth-child(6) .go-bar-women {
  transition: transform 250ms cubic-bezier(0, 0, 0.56, 1.26) 1500ms;
}

.go-bar:nth-child(6) .go-bar-percentage {
  transition: opacity 250ms ease-out 1500ms;
}

.go-bar:nth-child(7) .go-bar-men {
  transition: transform 500ms cubic-bezier(0, 0, 0.56, 1.26) 1500ms;
}

.go-bar:nth-child(7) .go-bar-women {
  transition: transform 250ms cubic-bezier(0, 0, 0.56, 1.26) 1750ms;
}

.go-bar:nth-child(7) .go-bar-percentage {
  transition: opacity 250ms ease-out 1750ms;
}

.go-bar.animated .go-bar-men, .go-bar.animated .go-bar-women {
  transform: scale(1, 1);
}

.go-bar.animated .go-bar-percentage {
  opacity: 1;
}

.go-bar-top {
  height: 100%;
  display: flex;
  align-items: flex-end;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
}

.go-bar-men {
  background-color: #509587;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  transform: scale(1, 0);
  transform-origin: bottom;
}

.go-bar-women {
  color: white;
  text-align: center;
  background-color: #9b5ea5;
  width: 100%;
  padding-bottom: 16px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: scale(1, 0);
  transform-origin: bottom;
}

.go-bar-percentage {
  opacity: 0;
}

.go-bar-year {
  color: black;
}

