.gto-bar-name {
  font-weight: 400;
  min-width: 254px;
  text-align: right;
  padding-right: 32px;
  box-sizing: content-box;
  opacity: 0;
}

.gto-bar-border {
  border-left: 1px solid #CCC;
  padding-bottom: 8px;
}

.gto-bar-background {
  color: white;
  background-color: #9b5ea5;
  padding-left: 8px;
  transform: scale(0, 1);
  transform-origin: left;
}

.animated .gto-bar-name {
  opacity: 1;
}

.animated .gto-bar-background {
  transform: scale(1, 1);
}

.gt-flex {
  display: flex;
}

.gt-content, .gt-rows {
  width: 50%;
}

.gt-square {
  width: calc(100% / 10 - 16px);
  transform: scale(0, 1);
  transform-origin: center;
}

.gt-square > div {
  background-color: #509587;
  padding-bottom: 100%;
}

.gt-square:nth-child(1) {
  transition: transform 150ms cubic-bezier(0, 0, 0.56, 1.26) 0ms;
}

.gt-square:nth-child(2) {
