}

.gt-squares-row {
  width: 100%;
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.gt-squares-row.animated .gt-square {
  transform: scale(1, 1);
}

.gt-squares-row:nth-child(10) .gt-square div {
  background-color: #9b5ea5;
}

.gt-squares-row:nth-child(9) .gt-square:nth-child(n+6) div {
  background-color: #9b5ea5;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  list-style: none;
}

body {
  font-family: 'Montserrat', Helvetica, sans-serif;
  color: black;
  font-size: 16px;
  line-height: 1.6em;
}

.container {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

@media (min-width: 768px) and (max-width: 1059px) {
  .container {
    max-width: 688px;
  }
}

@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }
}

.air-1 {
  margin-bottom: 16px;
}

.air-2 {
  margin-bottom: 32px;
}

.button {
  padding: 16px 32px;
  background-color: #9b5ea5;
  margin: 0 auto;
}

.button-link {
  color: white;
  text-decoration: none;
}

.graph-title {
  font-size: 42px;
  line-height: 1.2em;
  font-weight: bold;
}

.graph-subtitle {
  font-size: 20px;
  font-weight: 300;
}

.graph-content {
  display: flex;
  flex-direction: column;
}

@media (max-width: 767px) {
  .graph-content {
    font-size: 12px;
  }
}
